import React from 'react'
import './LayoutStyle.css'
import Header from './Header'
import { StaticQuery } from 'gatsby'
import { Helmet } from "react-helmet"
import favicon from "../images/favicon-96x96.png"

class Layout extends React.Component{
    render(){
        return(
            
            <div className="layout__container">

                <StaticQuery query={graphql`
                query {
                    site {
                        siteMetadata {
                            title
                        }
                    }
              }`
            } render={data=>(
                
                    <Header logo={data.site.siteMetadata.title}/>
                   
                )}/>

            {this.props.children}
            <Helmet>
            <meta charSet="utf-8" />
            <meta name="description" content="Amatorski blog opowiadający o IT połączony z portfolio. Opowiadam na nim o technologiach webowych, głównie tych związanych z JavaScript, pojawiają się jednak także tematy związane z innymi językami, oraz ogólnymi zagadnieniami IT. Służy on także jako moje osobiste portfolio."/>
            <meta name="keywords" content="programowania, web development, front end, back end, Kamil Michna, Michna Kamil, programista Krosno, portfolio, javascript, ES6, js, koa.js, express.js"/>
            <title>Kamil Michna</title>
            <link rel="shortcut icon" type="image/png" href={favicon}/>
            </Helmet>   
            </div>
        
        )
    }
} 

export default Layout
